    .navbar .left-side a {
      font-size: 25px;
    }

    .navbar .text a{
      font-size: 12px;
    }

    .navbar .text button {
      font-size: 12px;
    }

  }

  @media only screen and (max-width: 950px) {

    .navbar {
      background-color: rgba(28, 28, 28, 1);
    }

    .navbar .left-side a {
      padding-top: 2.2px;
      font-size: 25px;
      font-family:'Bebas Neue', Times, serif;
      font-style:normal;
      font-weight: bold;
      padding-left: 8px;
    }

    .navbar .right-side .dropdown-button button{
      font-family:'Bebas Neue', Times, serif;
      color: #b59761;
      font-size: 23px;
      display: flex;
      flex: 70% 1;
      justify-content: right;
      /* padding-right: 30px; */
      padding-top: 8px;
      padding-left: 14px;
      font-weight: bold;
      padding-right: 10px;
    }

    .navbar .right-side .text {
      display: none;
    }
    
    .navbar .right-side #hidden {
      z-index: 2;
      position: absolute;
      
      /* align-items: right; */
      right: 0px;
      top: 11vh;
      height: auto;
      width: 50%;
      display: flex;
      flex-direction: column;
      background-color: rgba(28, 28, 28, 1);
      /* float: right; */
      padding-left: 10px;
    }

    .navbar .right-side #hidden a{
      justify-content:end;
      align-content: right;
      font-family:'Bebas Neue', Times, serif;
      padding: 4px;
      font-size: 25px;
      font-weight: bold;
    }

    .navbar .right-side #hidden button {
 
      font-family:'Bebas Neue', Times, serif;
      padding: 4px;
      font-size: 25px;
      font-weight: bold;
    }
  }
